
import React from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import Gmap from './gmap';

class BrewerySearch extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      categories: [],
      region: "",
      latArr: [],
      lngArr: []
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

  }

  handleChange(event) {
    const region=event.target.value;
    this.setState({
      region: region
    });
  }



handleSubmit(event) {
  axios.get('http://localhost:9078/api/proxy/breweries/' + this.state.region)
    .then(res => {
      const categories = res.data.data;
      const latArr = [];
      const lngArr = [];
      categories.map(category => {
        latArr.push(category.latitude);
        lngArr.push(category.longitude);
      });

      this.setState({
        categories:categories,
        latArr: latArr,
        lngArr: lngArr
      });

    });

    event.preventDefault();
}


render() {
  return (
    <div>
      <h1>BEEEEERRRRRR!!!</h1>
      <form onSubmit={this.handleSubmit}>
        <input type="text" placeholder="region" value={this.state.region} onChange={this.handleChange.bind(this)}/>
        <input type="submit" value="submit" onChange={this.handleSubmit.bind(this)}/>
      </form>

      <ul>
        {this.state.categories.map(category =>
          <li key={category.brewery.name}>{category.brewery.name}</li>
        )}
      </ul>
      <Gmap categories={this.state.categories} lat={this.state.latArr} lng={this.state.lngArr} />
    </div>
    );
  }
}

export default BrewerySearch;



